#skills-main {
    padding: 60px 60px;
    background-color: #02262e;
    color: white;
}

.skills {
    margin: 0;
    padding: 0;
    list-style-type: none;
    text-align: left;
}

.skills > li {

    /* display: inline-block; */
    display: table;
    padding: 5px 15px;
    font-size: 90%;
    /* background-color: #7d968e; */
    color:  white;
}

.skills > li > p {
    display: block;
}

.skills-subheader {
    text-align: left;
    margin-top: 20px;
    color: #abc4bc;
}

@media screen  and (max-width: 992px){
    .skills > li {

        display: inline-block;
        border: 1px solid #abc4bc;
        border-radius: 20px;
        margin: 5px;
        padding: 10px;
        font-size: 90%;
        /* background-color: #7d968e; */
        color:  white;
    }
}
