#landing-main {
    margin: 0;
    height: 100vh;
    width: 100vw;
    background-color: #00151a;
    font-size: 200%;
    color: white;
    overflow: hidden;
}

#landing-holder {
    position: absolute;
    z-index: 1000;
}

#landing-main .name {
    color: #34eb92;
}

#landing-main .major {
    /* color: #e3a76b;   */
}

#landing-main .university {
    /* color: #e3a76b;     */
}

.vertical-center {
    min-height: 100%;  /* Fallback for browsers do NOT support vh unit */
    min-height: 100vh; /* These two lines are counted as one :-)       */
  
    display: flex;
    align-items: center;
}

#preloader-canvas {
    z-index: 0;
    position: relative;
    width: 100vw;
    height: 100vh;
}

.landing-btn {

    font-size: 65%;
    border: 2px solid white;
    color: white;
    margin: 70px;
    padding: 10px 20px;
}

.landing-btn:hover {

    border: 2px solid #67b5a4;
    background-color: #67b5a4;
    color: white;
    text-decoration: none;
}