.App {
	text-align: center;
	overflow: hidden;
	background-color: #e3e3e3;
}

.heading-title {
    font-weight: bold;
    display: inline-block;
    padding-bottom: 5px;
	position: relative;
}

.heading-title:before{
    content: "";
    position: absolute;
    width: 50%;
    height: 1px;
    bottom: 0;
	left: 25%;
	border-radius: 2px;
    border-bottom: 4px solid black;
}